.calendar-input{width:600px}.calendar{width:100%;position:relative}.calendar .calendar-pannel{position:absolute;top:100%;left:0;z-index:1000;float:left;min-width:10rem;padding:.5rem 0;margin:.125rem 0 0;font-size:1rem;color:#292b2c;background-color:#fff;border:1px solid rgba(0,0,0,0.15);border-radius:2px}.calendar .calendar-pannel .calendar-item{display:inline-block}.calendar .calendar-pannel .calendar-item:nth-child(2):before{content:"";position:absolute;width:1px;height:230px;background:rgba(0,0,0,0.15);left:50%;top:39%;margin-top:-90px}.calendar .calendar-pannel .calendar-item:nth-child(2):after{content:"TO";position:absolute;width:24px;height:28px;left:50%;top:82%;margin-top:-90px;margin-left:-10px;font-size:16px;color:#888;line-height:28px;background-color:#fff}


